import { useState, useEffect } from 'react'
import { Button } from "@/components/ui/button"
import { ScrollArea } from "@/components/ui/scroll-area"
import { Home, Ship, Truck, Globe, Clock, Shield, Package, Phone } from 'lucide-react'

const navItems = [
  { icon: Home, label: 'Home', href: '#home' },
  { icon: Ship, label: 'Services', href: '#services' },
  { icon: Globe, label: 'Why Us', href: '#why-us' },
  { icon: Package, label: 'Gallery', href: '#gallery' },
  { icon: Phone, label: 'Contact', href: '#contact' },
]

export function Sidebar() {
  const [isOpen, setIsOpen] = useState(false)

  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth >= 768) {
        setIsOpen(true)
      } else {
        setIsOpen(false)
      }
    }

    window.addEventListener('resize', handleResize)
    handleResize()

    return () => window.removeEventListener('resize', handleResize)
  }, [])

  return (
    <>
      <Button
        variant="outline"
        size="icon"
        className="fixed top-4 left-4 z-50 md:hidden"
        onClick={() => setIsOpen(!isOpen)}
      >
        {isOpen ? <Home className="h-4 w-4" /> : <Package className="h-4 w-4" />}
      </Button>
      <div className={`fixed inset-y-0 left-0 transform ${isOpen ? 'translate-x-0' : '-translate-x-full'} w-64 bg-white shadow-lg transition-transform duration-300 ease-in-out z-40`}>
        <ScrollArea className="h-full py-6 pl-6 pr-1">
          <div className="space-y-4">
            {navItems.map((item, index) => (
              <a
                key={index}
                href={item.href}
                className="flex items-center gap-3 rounded-lg px-3 py-2 text-gray-500 transition-all hover:text-gray-900 hover:bg-gray-100"
                onClick={() => setIsOpen(false)}
              >
                <item.icon className="h-5 w-5" />
                {item.label}
              </a>
            ))}
          </div>
        </ScrollArea>
      </div>
    </>
  )
}

